import Link from "next/link";
import cases from "@/data/mock_cases.json";
import { CaseSchema } from "@/lib/schema";
import { Badge } from "@/components/ui/badge";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";

function badgeVariant(label: string) {
  if (label === "BLOCK") return "destructive";
  if (label === "ESCALATE") return "secondary";
  return "default";
}

export default async function CasesPage() {
  const parsed = (cases as unknown[]).map((c) => CaseSchema.parse(c));

  return (
    <div className="p-6 space-y-6 max-w-6xl">
      <div className="space-y-1">
        <h1 className="text-2xl font-semibold">HITL Review Inbox</h1>
        <p className="text-sm text-muted-foreground">
          Review and authorize high-impact tool calls.
        </p>
      </div>

      <Card>
        <CardHeader>
          <CardTitle className="text-base">Queue</CardTitle>
        </CardHeader>
        <CardContent>
          <Table>
            <TableHeader>
              <TableRow>
                <TableHead>Case ID</TableHead>
                <TableHead>User</TableHead>
                <TableHead>Tool</TableHead>
                <TableHead>Verdict</TableHead>
                <TableHead className="text-right">Score</TableHead>
                <TableHead>Status</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {parsed.map((c) => (
                <TableRow key={c.id}>
                  <TableCell>
                    <Link className="underline" href={`/cases/${c.id}`}>
                      {c.id}
                    </Link>
                  </TableCell>
                  <TableCell>{c.user_display}</TableCell>
                  <TableCell className="font-mono text-xs">{c.tool_name}</TableCell>
                  <TableCell>
                    <Badge variant={badgeVariant(c.risk_label)}>{c.risk_label}</Badge>
                  </TableCell>
                  <TableCell className="text-right">{c.risk_score}</TableCell>
                  <TableCell className="text-xs text-muted-foreground">{c.status}</TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </CardContent>
      </Card>
    </div>
  );
}
